/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import FieldGenerator from 'components/FormsV3/FieldGenerator';
import ShareableProductInfo from '../ShareableProductInfo/ShareableProductInfo';

import { holderFlowForms } from './ShareablePopUp.constants';

import './ShareableFirstVisitPopup.scss';

class FirstVisitForm1 extends Component {
  render() {
    const {
      formValues,
      formErrors,
      generateLink,
      onChangeValue,
      valuesToListen,
      flowValues,
      conditions,
      productInfo,
    } = this.props;

    return (
      <Row noGutters>
        <Col xs={12}>
          <h3>Create a shareable link to share with other insured!</h3>
        </Col>
        <Col xs={12}>
          <ShareableProductInfo
            valuesToListen={valuesToListen}
            flowValues={flowValues}
            conditions={conditions}
            productInfo={productInfo}
          />
        </Col>
        <Col xs={12}>
          <Row noGutters>
            {
              _.map(holderFlowForms, (form, index) => (
                <FieldGenerator
                  key={`business-form-${index}`}
                  values={formValues}
                  errors={formErrors}
                  fieldData={form}
                  onChangeValue={(type, name, value) => onChangeValue(type, name, value, true)}
                />
              ))
            }
          </Row>
        </Col>
        <Col className="d-flex justify-content-center mt-4" xs={12}>
          <Button
            type="button"
            variant="medium-flat-green"
            height={48}
            width={168}
            onClick={generateLink}
          >
            <p>Next</p>
          </Button>
        </Col>
      </Row>
    );
  }
}

FirstVisitForm1.defaultProps = {
  conditions: null,
};

FirstVisitForm1.propTypes = {
  formValues: PropTypes.object.isRequired,
  formErrors: PropTypes.object.isRequired,
  generateLink: PropTypes.func.isRequired,
  onChangeValue: PropTypes.func.isRequired,
  valuesToListen: PropTypes.array.isRequired,
  flowValues: PropTypes.object.isRequired,
  conditions: PropTypes.object,
  productInfo: PropTypes.object.isRequired,
};

export default FirstVisitForm1;
