/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
  Tooltip,
} from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import TextInput from 'components/FormsV3/TextInput/TextInput';

import { CopyToClipboard } from 'react-copy-to-clipboard';
import './ShareableFirstVisitPopup.scss';

class FirstVisitForm2 extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isCopied: false,
    };
  }

  render() {
    const { isCopied } = this.state;

    const {
      emailValues,
      emailErrors,
      url,
      emailCount,
      onChangeValue,
      handleRemoveEmail,
      setEmailCount,
      setFormState,
      sendEmails,
    } = this.props;

    return (
      <Row noGutters>
        <Col xs={12}>
          <h3>Share this link</h3>
          <div className="d-flex shareable-link">
            <TextInput
              type="text"
              value={url}
              onChange={() => {}}
              name="share-link"
              readOnly
              noInternalDesc
              placeholderString="Share link"
            />
            <CopyToClipboard
              text={url}
              onCopy={() => this.setState({ isCopied: true }, () => setTimeout(() => this.setState({ isCopied: false }), 3000))}
            >
              <button>
                <i id="shareable-link-tooltip" className="far fa-copy" />
              </button>
            </CopyToClipboard>
            <Tooltip
              target="shareable-link-tooltip"
              isOpen={isCopied}
              toggle={() => this.setState({ isCopied: false })}
              className="animated fade"
            >
              <span>Copied!</span>
            </Tooltip>
          </div>
        </Col>
        <Col className="mt-4" xs={12}>
          <h3>Mail this link</h3>
          {
            _.map(_.range(0, emailCount), (num) => {
              const key = `email-${num}`;

              return (
                <div key={key} className="d-flex email-container">
                  <TextInput
                    type="email"
                    value={(emailValues[key] && emailValues[key].value) || ''}
                    errorString={emailErrors[key]}
                    onChange={(type, name, value) => onChangeValue(type, name, value, false)}
                    name={key}
                    placeholderString="john.doe@email.com"
                    noInternalDesc
                  />
                  <button
                    type="button"
                    onClick={() => handleRemoveEmail(num)}
                  >
                    <i className="fa fa-minus-circle" />
                  </button>
                </div>
              );
            })
          }
          <button
            className="add-button"
            type="button"
            onClick={() => setEmailCount(emailCount + 1)}
          >
            <p><i className="fa fa-plus" /><span>Add insured</span></p>
          </button>
        </Col>
        <Col className="d-flex justify-content-center button-container" xs={12}>
          <Button
            type="button"
            variant="medium-outline-inverse"
            height={48}
            width={168}
            onClick={() => setFormState(0)}
          >
            <p>Back</p>
          </Button>
          <Button
            type="button"
            variant="medium-flat-green"
            height={48}
            width={168}
            onClick={sendEmails}
          >
            <p>Send</p>
          </Button>
        </Col>
      </Row>
    );
  }
}

FirstVisitForm2.defaultProps = {
  url: '',
};

FirstVisitForm2.propTypes = {
  emailValues: PropTypes.object.isRequired,
  emailErrors: PropTypes.object.isRequired,
  url: PropTypes.string,
  emailCount: PropTypes.number.isRequired,
  onChangeValue: PropTypes.func.isRequired,
  handleRemoveEmail: PropTypes.func.isRequired,
  setEmailCount: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  sendEmails: PropTypes.func.isRequired,
};

export default FirstVisitForm2;
