/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import APILoader from 'components/Templates/APILoader/APILoader';
import CheckoutComplete from 'components/Templates/CheckoutComplete/CheckoutComplete';

import { validateFormValue, validateForm } from 'lib/validatorsV2';
import { getFormValue } from 'lib/helperFunctions';

import { getGenerateLink, sendEmails } from 'actions/shareableLinkActions';

import { holderFlowForms } from './ShareablePopUp.constants';

import FirstVisitForm1 from './FirstVisitForm1';
import FirstVisitForm2 from './FirstVisitForm2';

import '../ShareablePopUp.scss';
import './ShareableFirstVisitPopup.scss';

const defaultState = {
  formValues: {},
  emailValues: {},
  formErrors: {},
  emailErrors: {},
  formState: 0,
  url: 'https://wwww.policypal.com',
  isCopied: false,

  emailCount: 1,
  sessionID: null,
};

class ShareableFirstVisitPopup extends Component {
  constructor(props) {
    super(props);

    this.state = { ...defaultState };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.handleRemoveEmail = this.handleRemoveEmail.bind(this);
    this.setFormState = this.setFormState.bind(this);
    this.setEmailCount = this.setEmailCount.bind(this);
    this.generateLink = this.generateLink.bind(this);
    this.sendEmails = this.sendEmails.bind(this);
  }

  componentWillUnmount() {
    this.setState({ ...defaultState });
  }

  async onChangeValue(type, name, value, isForm) {
    const {
      formValues,
      formErrors,
      emailValues,
      emailErrors,
    } = this.state;

    const valuesToUse = isForm ? formValues : emailValues;
    const errorsToUse = isForm ? formErrors : emailErrors;
    const formToUse = isForm ? holderFlowForms : [{ slug: name, is_required: true }];

    const error = errorsToUse && errorsToUse[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, valuesToUse, formToUse) };
    }

    const valueName = isForm ? 'formValues' : 'emailValues';
    const errorName = isForm ? 'formErrors' : 'emailErrors';

    this.setState({
      [valueName]: {
        ...this.state[valueName],
        [name]: { type, value },
      },
      [errorName]: {
        ...this.state[errorName],
        ...newError,
      },
    });
  }

  setFormState(formState) {
    this.setState({ formState });
  }

  setEmailCount(emailCount) {
    this.setState({ emailCount });
  }

  async generateLink() {
    const { formValues } = this.state;
    const { getAnonymousSession, getSessionParams } = this.props;

    try {
      const newErrors = await validateForm(formValues, null, holderFlowForms);

      if (!_.isEmpty(newErrors)) {
        this.setState({
          formErrors: newErrors,
        });

        return;
      }

      console.log(getSessionParams());

      const sessionRequest = {
        sessionParams: {
          ...getSessionParams(),
          email: getFormValue(formValues.email),
        },
      };

      const sessionID = await getAnonymousSession(sessionRequest, true)();

      if (sessionID) {
        const linkRequest = {
          session_token: sessionID,
          duration: getFormValue(formValues.duration),
          holder_info: {
            first_name: getFormValue(formValues.first_name),
            last_name: getFormValue(formValues.last_name),
            email: getFormValue(formValues.email),
            birth_date: getFormValue(formValues.birth_date).toISOString(),
          },
        };

        const linkResult = await this.APILoader.handleAPI(() => getGenerateLink(linkRequest));
        if (linkResult) {
          this.setState({
            url: linkResult.link,
            sessionID,
          });
        }

        this.setFormState(1);
      }
    } catch (error) {
      // eslint-disable-next-line
      console.log(error);
    }
  }

  async sendEmails() {
    const { emailValues, emailCount, sessionID } = this.state;

    if (!sessionID) {
      return;
    }

    try {
      const forms = _.map(_.range(0, emailCount), num => ({
        slug: `email-${num}`,
        type: 'email',
        is_required: true,
      }));

      const newErrors = await validateForm(emailValues, null, forms);

      if (!_.isEmpty(newErrors)) {
        this.setState({
          emailErrors: newErrors,
        });

        return;
      }

      const receipients = _.map(_.range(0, emailCount), num => emailValues[`email-${num}`].value);

      const request = {
        session_token: sessionID,
        receipients,
      };

      const sendResult = await this.APILoader.handleAPI(() => sendEmails(request));

      if (sendResult) {
        this.checkoutComplete.openCheckoutComplete();
      }
      console.log(sendResult);

      // if (sessionID) {
      //   const linkRequest = {
      //     session_token: sessionID,
      //     duration: getFormValue(formValues.duration),
      //     holder_info: {
      //       first_name: getFormValue(formValues.first_name),
      //       last_name: getFormValue(formValues.last_name),
      //       email: getFormValue(formValues.email),
      //       birth_date: getFormValue(formValues.birth_date).toISOString(),
      //     },
      //   };

      //   const linkResult = await getGenerateLink(linkRequest);
      //   if (linkResult) {
      //     this.setState({
      //       url: linkResult.link,
      //     });
      //   }

      //   this.setFormState(1);
      // }
    } catch (error) {
      // eslint-disable-next-line
      console.log(error);
    }
  }

  handleRemoveEmail(index) {
    const { emailValues, emailErrors, emailCount } = this.state;

    if (emailCount === 1) {
      return;
    }

    const newEmailValues = {};
    const newEmailErrors = {};

    for (let i = 0; i < emailCount; ++i) {
      const key = `email-${i}`;
      if (i < index) {
        newEmailValues[key] = emailValues[key];
        newEmailErrors[key] = emailErrors[key];
      } else if (i > index) {
        newEmailValues[`email-${i - 1}`] = emailValues[key];
        newEmailErrors[`email-${i - 1}`] = emailErrors[key];
      }
    }

    this.setState({
      emailValues: newEmailValues,
      emailErrors: newEmailErrors,
      emailCount: emailCount - 1,
    });
  }

  render() {
    const {
      formState,
      formValues,
      formErrors,
      emailValues,
      emailErrors,
      url,
      emailCount,
    } = this.state;

    const {
      showModal,
      closeModal,
      valuesToListen,
      values: flowValues,
      conditions,
      productInfo,
    } = this.props;

    return (
      <ModalPopup
        showModal={showModal}
        closeModal={closeModal}
        className="shareable-link-popup shareable-first-visit"
        backdropClassName="blue-modal-backdrop"
        header={<p>Policy Holder</p>}
        body={(
          <Fragment>
            {
              formState === 0 ?
                <FirstVisitForm1
                  formValues={formValues}
                  formErrors={formErrors}
                  generateLink={this.generateLink}
                  onChangeValue={this.onChangeValue}
                  valuesToListen={valuesToListen}
                  flowValues={flowValues}
                  conditions={conditions}
                  productInfo={productInfo}
                />
              :
                <FirstVisitForm2
                  emailValues={emailValues}
                  emailErrors={emailErrors}
                  url={url}
                  emailCount={emailCount}
                  onChangeValue={this.onChangeValue}
                  handleRemoveEmail={this.handleRemoveEmail}
                  setEmailCount={this.setEmailCount}
                  setFormState={this.setFormState}
                  sendEmails={this.sendEmails}
                />
            }
            <APILoader ref={(node) => { this.APILoader = node; }} />
            <CheckoutComplete
              className="shareable-complete"
              isQuoteOnly
              upperText="Email sent!"
              midText="Please wait for your group to fill up their personal details"
              buttonText="OK"
              wrappedComponentRef={node => this.checkoutComplete = node}
              callback={closeModal}
            />
          </Fragment>
        )}
      />
    );
  }
}

ShareableFirstVisitPopup.defaultProps = {
  showModal: false,
  conditions: null,
};

ShareableFirstVisitPopup.propTypes = {
  showModal: PropTypes.bool,
  closeModal: PropTypes.func.isRequired,
  valuesToListen: PropTypes.array.isRequired,
  values: PropTypes.object.isRequired,
  conditions: PropTypes.object,
  productInfo: PropTypes.object.isRequired,
  getAnonymousSession: PropTypes.func.isRequired,
  getSessionParams: PropTypes.func.isRequired,
};

export default ShareableFirstVisitPopup;
