/**
 * TravelDetailContainer.js
 * The detail container for travel
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import moment from 'moment';
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactPixel from 'react-facebook-pixel';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import RedirectPopup from 'components/Templates/RedirectPopup/RedirectPopup';
import TravelDetail from 'components/Travel/TravelDetail';

import { getAddToCartData } from 'lib/trackingHelper';
import { setCategoryNavBar } from 'actions/navActions';
import { postToRedirect, validateRestrictions, getSessionParams } from 'actions/travelActions';
import { setInsuranceFormState, getAnonymousSession, getPostal, FORMSTATES } from 'actions/insuranceActions';
import { setRedirectPopup } from 'actions/redirectActions';
import { verifyHolder, verifyInsured, postCreateInsured, putInsured, deleteInsured } from 'actions/shareableLinkActions';

import { gaTag, gaEvent, ampTag } from 'lib/tagFunctions';
import { getFormValue, revertFormValues } from 'lib/helperFunctions';

import {
  normalIndividualFields,
  adultFields,
  childFields,
  getValuesToListen,
  conditions,
  getIndividualFields,
} from './Travel.constants';

class TravelDetailContainer extends Component {
  constructor(props) {
    super(props);

    const { values, selectedPlanData, planType } = this.props;

    let planDetails = {
      logo: Logos.others,
      logoDesc: '',
      planName: '',
      planTotal: null,
    };

    try {
      let discountTripType = 'default';
      const tripType = getFormValue(values.trip_type);
      if (tripType) {
        discountTripType = tripType === 'Single Trip' ? 'single' : 'annual';
      }

      const { premium } = selectedPlanData.policy_info[planType];
      const discountData = selectedPlanData.info.discount ? selectedPlanData.info.discount[discountTripType] : null;
      const discount = discountData ? premium.price * discountData.amount : null;

      planDetails = {
        logo: Logos[selectedPlanData.info.insurer_slug],
        logoDesc: `${selectedPlanData.info.insurer_name}.svg`,
        planName: selectedPlanData.info.product_name,
        planTotal: `$${premium.net_price.toFixed(2)}`,
        planSubTotal: `$${premium.price.toFixed(2)}`,
        discountPercentage: discountData ? discountData.display : null,
        discountAmount: discount && discount !== 0 ? `$${(premium.price * discountData.amount).toFixed(2)}` : null,
        cashback: selectedPlanData.info.cashback ? `$${(selectedPlanData.info.cashback * premium.net_price).toFixed(2)}` : null,
      };
    } catch (error) {
      // eslint-disable-next-line
      console.log(error);
    }

    console.log(planDetails, selectedPlanData);
    if (values) {
      this.state = {
        planData: {
          planType,
          numAdults: getFormValue(values.num_adult),
          numChildren: getFormValue(values.num_children),
        },
        planDetails,
        shareToFillData: null,
      };
    }

    this.travelRedirect = this.travelRedirect.bind(this);
    this.submitCallback = this.submitCallback.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleUpdateInsured = this.handleUpdateInsured.bind(this);
    this.handlePostCode = this.handlePostCode.bind(this);

    this.setShareToFillParams = this.setShareToFillParams.bind(this);
    this.deleteShareToFillUser = this.deleteShareToFillUser.bind(this);
  }

  componentDidMount() {
    const { setFormState, doSetCategoryNavBar, selectedPlanData, planType, values, defaultRoute } = this.props;
    const { planTotal } = this.state.planDetails;

    setFormState(FORMSTATES.DETAILFORM.index);

    if (selectedPlanData) {
      const trackData = getAddToCartData({
        ..._.pick(selectedPlanData.info, ['insurer_slug', 'insurer_name', 'product_slug', 'product_name']),
        values,
        coverage_level: planType,
        price: planTotal,
      });
      ReactPixel.track('AddToCart', trackData);
    }

    gaTag({
      page_title: 'Travel - Detail Form',
      page_path: `/${defaultRoute}/detailform`,
    });
    ampTag('TravelDetailsS');
    document.body.style.backgroundColor = '#F9FAFF';
    doSetCategoryNavBar(true);
  }

  componentWillUnmount() {
    const { doSetCategoryNavBar } = this.props;
    document.body.style.backgroundColor = '#FFFFFF';
    doSetCategoryNavBar(false);
  }

  async handlePostCode(values, onChangeDetailValue, formName = null) {
    try {
      if (values) {
        const postcode = values.postal.value;
        const result = await getPostal(postcode);
        if (result) {
          // const streetName = result.name === '' ? '' : `${result.name} `;
          const street = result.street === '' ? '' : `${result.street} `;
          const block = result.block === '' ? '' : `${result.block} `;
          // let concatAddress = `${block}${streetName}${street}`;
          let concatAddress = `${block}${street}`;

          onChangeDetailValue('text', 'blk_no', result.block, formName);
          concatAddress = street;

          onChangeDetailValue('text', 'address', concatAddress, formName);
        }
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  async handleSubmit(tabView) {
    const {
      values,
      detailValues,
      selectedPlanData,
      validateForm,
    } = this.props;

    try {
      const numAdults = getFormValue(values.num_adult);
      const numChildren = getFormValue(values.num_children);

      const iFields = getIndividualFields(selectedPlanData);

      const validateRestrictionParams = {
        plan_type: getFormValue(values.plan_type),
        trip_type: getFormValue(values.trip_type),
        num_adult: numAdults.toString(),
        num_children: numChildren.toString(),
        insurer_slug: selectedPlanData.info.insurer_slug,
        product_name: selectedPlanData.info.product_name,
        birth_dates: {
          adults: [],
          children: [],
        },
      };

      const adultName = getFormValue(values.plan_type) === 'Group' ? 'Person' : 'Adult';

      let errorArray = null;
      const errorPersons = [];
      let errorTabIndex = null;

      let validated = await validateForm('adult_0', null, iFields.fields, { insurer_slug: selectedPlanData.info.insurer_slug });
      if (!validated) {
        errorPersons.push(`${adultName} 1`);
        errorTabIndex = 0;
      } else {
        validateRestrictionParams.birth_dates.adults.push(moment(getFormValue(detailValues.adult_0.birth_date)).format('DD/MM/YYYY'));
      }

      for (let i = 1; i < numAdults; ++i) {
        const name = `adult_${i}`;
        // eslint-disable-next-line
        const validateResult = await validateForm(name, null, adultFields);
        validated = validated ? validateResult : false;

        if (!validateResult) {
          errorPersons.push(`${adultName} ${i + 1}`);
          errorTabIndex = i;
        } else {
          validateRestrictionParams.birth_dates.adults.push(moment(getFormValue(detailValues[name].birth_date)).format('DD/MM/YYYY'));
        }
      }

      for (let i = 0; i < numChildren; ++i) {
        const name = `child_${i}`;
        // eslint-disable-next-line
        const validateResult = await validateForm(name, null, childFields);
        validated = validated ? validateResult : false;

        if (!validateResult) {
          errorPersons.push(`Child ${i + 1}`);
          errorTabIndex = numAdults + i;
        } else {
          validateRestrictionParams.birth_dates.children.push(moment(getFormValue(detailValues[name].birth_date)).format('DD/MM/YYYY'));
        }
      }

      if (!validated && errorPersons.length > 0) {
        errorArray = _.reduce(errorPersons, (result, person, index) => {
          const isLast = index === errorPersons.length - 1;
          return `${result} ${person}${isLast ? '' : ','}`;
        }, 'Forms not filled for: ');
        errorArray = [<p className="error-string" key="error-1">{errorArray}</p>];
      }

      let restrictionValid = false;
      if (validated) {
        const restrictionResult = await validateRestrictions(validateRestrictionParams);
        restrictionValid = restrictionResult && restrictionResult.valid;

        if (!restrictionResult) {
          errorArray = [<p className="error-string" key="error-1">Birth date validation failed! Please try again.</p>];
        } else if (!restrictionResult.valid) {
          errorArray = _.map(restrictionResult.errors, (error, index) => (
            <p className="error-string" key={`error-${index}`}>{error}</p>
          ));
        }

        if (restrictionValid) {
          gaEvent('Travel - DetailForm - Checkout', {
            event_category: 'Travel',
            event_label: 'Travel - DetailForm - Checkout',
          });
          ampTag('TravelCheckoutA', {
            idtype: getFormValue(detailValues.adult_0.id_type),
            nationality: getFormValue(detailValues.adult_0.nationality),
            gender: getFormValue(detailValues.adult_0.gender),
            marital: getFormValue(detailValues.adult_0.marital_status),
            race: getFormValue(detailValues.adult_0.race),
            occupation: getFormValue(detailValues.adult_0.occupation),
          });

          this.submitCallback();
          return [];
        }
      }

      if (tabView && errorTabIndex !== null) {
        tabView.wrappedInstance.goToIndex(errorTabIndex);
      }

      if (!restrictionValid) {
        window.scrollTo(0, document.body.scrollHeight);
      }

      return errorArray;
    } catch (error) {
      // eslint-disable-next-line
    }

    return [];
  }

  async handleUpdateInsured(formName) {
    const { shareToFillData } = this.state;

    console.log(formName);

    const {
      values,
      detailValues,
      selectedPlanData,
      validateForm,
      shareToFillUser,
    } = this.props;

    const validateRestrictionParams = {
      plan_type: getFormValue(values.plan_type),
      trip_type: getFormValue(values.trip_type),
      num_adult: '3',
      num_children: '0',
      insurer_slug: selectedPlanData.info.insurer_slug,
      product_name: selectedPlanData.info.product_name,
      birth_dates: {
        adults: [],
        children: [],
      },
    };

    const isHolder = shareToFillUser === 'holder';
    const fieldsToUse = isHolder && formName === 'adult_0' ? normalIndividualFields : adultFields;
    // const fields = _.map(fieldsToUse, field => ({ ...field, is_required: false }));

    const isValidated = await validateForm(formName, null, fieldsToUse);
    let errorString = isValidated ? null : 'Please verify your information again.';

    if (isValidated) {
      const birthDate = detailValues[formName] && detailValues[formName].birth_date;
      let restrictionResult = { valid: true };

      if (birthDate) {
        const birthDateString = moment(getFormValue(detailValues[formName] && detailValues[formName].birth_date)).format('DD/MM/YYYY');
        validateRestrictionParams.birth_dates.adults = [birthDateString, birthDateString, birthDateString];
        restrictionResult = await validateRestrictions(validateRestrictionParams);
      }

      if (!restrictionResult) {
        errorString = 'Birth date validation failed! Please try again';
      } else if (!restrictionResult.valid && restrictionResult.errors.length > 0) {
        // eslint-disable-next-line
        errorString = restrictionResult.errors[0];
      } else {
        const request = _.reduce(fieldsToUse, (result, field) => {
          const newResult = { ...result };
          let value = getFormValue(detailValues[formName] && detailValues[formName][field.slug]);
          if (value && field.type === 'date') {
            value = moment(value).format('DD/MM/YYYY');
          }
          newResult[field.slug] = value || '';

          return newResult;
        }, {});

        console.log(shareToFillData, request);
        const index = parseInt(formName.split('_')[1], 10);
        // eslint-disable-next-line
        const insuredInfo = isHolder && index === 0 ? shareToFillData.holder_info :
          !isHolder && index === 0 ? shareToFillData.insured_info[0] :
          shareToFillData.insured_info[index - 1];
        const putResults = await putInsured(insuredInfo.token, request, false);

        if (!putResults) {
          errorString = 'Update information failed! Please try again.';
        } else {
          if (putResults.updated) {
            errorString = 'Results updated!';
          }
        }
      }
    }

    return errorString && [<p className="error-string" key="error-1">{errorString}</p>];
  }

  setShareToFillParams(shareToFillData) {
    console.log(adultFields, shareToFillData);
    const { onUpdateDetailValues, shareToFillUser } = this.props;

    let detailValues = null;

    if (shareToFillUser === 'holder') {
      detailValues = _.reduce(shareToFillData.insured_info, (result, info, index) => {
        const newResult = { ...result };
        newResult[`adult_${index + 1}`] = revertFormValues(adultFields, info);
        return newResult;
      }, { adult_0: revertFormValues(normalIndividualFields, shareToFillData.holder_info) });
    } else {
      detailValues = { adult_0: revertFormValues(adultFields, shareToFillData.insured_info[0]) };
    }

    console.log(detailValues);
    onUpdateDetailValues(detailValues);

    this.setState({ shareToFillData });
  }

  async deleteShareToFillUser(userIndex) {
    const { shareToFillData, planData } = this.state;
    const { onUpdateDetailValues, detailValues, onUpdateValues, values, onUpdateSessionDataInsured } = this.props;

    const formName = `adult_${userIndex}`;
    if (values.num_adult.value > 1) {
      // eslint-disable-next-line
      const insuredInfo = shareToFillData.insured_info[userIndex - 1];
      const result = await deleteInsured(insuredInfo.token);
      console.log(userIndex, insuredInfo, result);

      if (result) {
        let index = 0;
        const newDetailValues = _.reduce(detailValues, (result, values, key) => {
          const newResult = { ...result };

          if (key !== formName) {
            newResult[`adult_${index}`] = values;
            index += 1;
          }

          return newResult;
        }, {});

        console.log(newDetailValues);

        onUpdateDetailValues(newDetailValues);

        const newValues = {...values};
        newValues.num_adult.value -= 1;
        onUpdateValues(newValues);

        const newPlanData = { ...planData };
        newPlanData.numAdults = newPlanData.numAdults - 1;

        this.setState({
          planData: newPlanData,
        });

        onUpdateSessionDataInsured(userIndex - 1);
      }
    }
  }

  submitCallback() {
    const { selectedPlanData, history, defaultRoute } = this.props;
    if (selectedPlanData.info.deep_link.integration_type === 'webview') {
      this.RedirectPop.openRedirectPopup();

      return;
    }

    this.props.setFormState(FORMSTATES.CHECKOUT.index);
    history.push(`/${defaultRoute}${FORMSTATES.CHECKOUT.path}`);
  }

  async travelRedirect() {
    const {
      values,
      detailValues,
      planType,
      selectedPlanData,
      handleAPI,
      doSetRedirectPopup,
      history,
      defaultRoute,
    } = this.props;


    const result = await handleAPI(() => postToRedirect(values, detailValues, selectedPlanData, planType));
    if (result) {
      if (selectedPlanData.info.insurer_slug === 'fwd') {
        doSetRedirectPopup(
          {
            insurer_slug: selectedPlanData.info.insurer_slug,
            insurer_name: selectedPlanData.info.insurer_name,
            product_name: selectedPlanData.info.product_name,
          },
          `Redirecting to ${selectedPlanData.info.insurer_name} - ${selectedPlanData.info.product_name}`,
          () => { window.location = result.url; },
        );
        history.push(`/${defaultRoute}/redirect?insurer=${selectedPlanData.info.insurer_slug}&product=${selectedPlanData.info.product_slug}&plantype=${planType}`);
        // window.location = result.url;
      } else if (selectedPlanData.info.insurer_slug === 'etiqa' || selectedPlanData.info.insurer_slug === 'tiq') {
        while (this.redirectButton.firstChild) {
          this.redirectButton.removeChild(this.redirectButton.firstChild);
        }
        const range = document.createRange();
        this.redirectButton.appendChild(range.createContextualFragment(result.form_html));

        setTimeout(() => window.$(`#${result.form_id}`).get(0).submit(), 300);
      }
    }
  }

  render() {
    const { planData, planDetails } = this.state;
    const { values, detailValues, selectedPlanData, planType } = this.props;


    return (
      <div className="travel-detail-container">
        <TravelDetail
          {...this.props}
          planData={planData}
          planDetails={planDetails}
          verifyHolder={verifyHolder}
          verifyInsured={verifyInsured}
          postCreateInsured={postCreateInsured}
          handleUpdateInsured={this.handleUpdateInsured}
          setShareToFillParams={this.setShareToFillParams}
          deleteShareToFillUser={this.deleteShareToFillUser}
          // changePlan={() => {
          //   history.goBack();
          //   gaEvent('Travel - DetailForm - ChangePlan', {
          //     event_category: 'Travel',
          //     event_label: 'Travel - DetailForm - ChangePlan',
          //   });
          // }}
          normalIndividualFields={normalIndividualFields}
          adultFields={adultFields}
          childFields={childFields}
          valuesToListen={getValuesToListen}
          conditions={conditions}
          getIndividualFields={getIndividualFields}
          handlePostCode={this.handlePostCode}
          handleSubmit={this.handleSubmit}
          getSessionParams={() => getSessionParams(values, null, selectedPlanData, planType)}
          getAnonymousSession={getAnonymousSession}
        />
        <RedirectPopup
          ref={node => this.RedirectPop = node}
          noEmail
          onConfirm={this.travelRedirect}
        />
        <div className="redirect-button" ref={node => this.redirectButton = node} />
      </div>
    );
  }
}

TravelDetailContainer.defaultProps = {
};

TravelDetailContainer.propTypes = {
  setFormState: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  selectedPlanData: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  doSetCategoryNavBar: PropTypes.func.isRequired,
  doSetRedirectPopup: PropTypes.func.isRequired,
};

// const mapStateToProps = state => ({
// });

const mapDispatchToProps = dispatch => ({
  // getAnonymousSession: (params) => {
  //   dispatch(getAnonymousSession(params, true));
  // },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  doSetCategoryNavBar: (isCategory) => {
    dispatch(setCategoryNavBar(isCategory));
  },
  doSetRedirectPopup: (productData, desc, func) => {
    dispatch(setRedirectPopup(productData, desc, func));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(TravelDetailContainer));
