/**
 * DetailForm.js
 * Wrapper that contains the piggy login and the
 * right container to checkout or choose plan
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import LabelImg from 'assets/images/forms/Label.svg';

// Import Insurer Logos
import PiggyFillForm from 'components/Templates/PiggySignUp/PiggyFillFormNew';
import ChangeInitialFormDataNew from 'components/Templates/ChangeInitialFormData/ChangeInitialFormDataNew';
import ShareablePopUp from 'components/Templates/ShareablePopUp/ShareableFirstVisitPopup/ShareableFirstVisitPopup';
import Insured from 'components/ShareableLink/Insured/Insured';
import Button from 'components/FormsV3/Button/Button';
import SingleCheckBox from 'components/FormsV3/SingleCheckBox/SingleCheckBox';

import { setIntercomVisibility } from 'actions/intercomActions';

import './DetailFormNew.scss';

class DetailFormNew extends Component {
  constructor(props) {
    super(props);

    const { shareToFillUser } = this.props;
    console.log(shareToFillUser);

    this.state = {
      rightStickyStyle: 'right-scroll',
      isSignUp: false,
      showModal: shareToFillUser === 'flow',
      isValidated: shareToFillUser !== 'holder' && shareToFillUser !== 'insured',
    };

    this.handlePreSubmit = this.handlePreSubmit.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.renderSignUp = this.renderSignUp.bind(this);
    this.renderSignUpButton = this.renderSignUpButton.bind(this);
    this.renderUpperRightContainer = this.renderUpperRightContainer.bind(this);
    this.renderBottomContainer = this.renderBottomContainer.bind(this);
  }

  componentDidMount() {
    const { doSetIntercomVisibility } = this.props;
    window.addEventListener('scroll', this.handleScroll);
    window.scrollTo(0, 0);
    this.handleScroll();

    doSetIntercomVisibility(false);
  }

  componentWillUnmount() {
    const { doSetIntercomVisibility } = this.props;
    doSetIntercomVisibility(true);

    window.removeEventListener('scroll', this.handleScroll);
  }

  handlePreSubmit(e) {
    const { isSignUp } = this.state;
    const { onSubmit, loginControl } = this.props;
    if (isSignUp) {
      loginControl.openPopup(1, () => onSubmit(e));
      return;
    }
    onSubmit(e);
  }

  handleScroll() {
    if (this.upperMarker) {
      const { rightStickyStyle } = this.state;
      const stickyStyle = this.upperMarker.getBoundingClientRect().y < 84 ? 'right-sticky' : 'right-scroll';

      if (rightStickyStyle !== stickyStyle) {
        this.setState({
          rightStickyStyle: stickyStyle,
        });
      }
    }
  }

  renderSignUp() {
    const {
      isLoggedIn,
      loginControl,
      piggyCallback,
      category,
      planDetails,
      shareToFillUser,
    } = this.props;

    if (shareToFillUser === 'insured') {
      return null;
    }

    return (
      <div className="piggy">
        <PiggyFillForm
          isLoggedIn={isLoggedIn}
          cashback={planDetails && planDetails.cashback}
          category={category}
          onClick={() => {
            loginControl.openPopup(1, piggyCallback);
          }}
        />
      </div>
    );
  }

  renderShareToFillInfo() {
    const { sessionData, deleteShareToFillUser } = this.props;

    if (sessionData) {
      return (
        <div
          className="share-to-fill-info upper-right-container"
        >
          <h5>People in the Group</h5>
          <p className="holder">{sessionData.holder_name} (Policy Holder)</p>
          {
            _.map(sessionData.insured || [], (insur, index) => (
              <p className="insured" key={`insured-${index}`}>
                {insur}
                <button
                  role="button"
                  onClick={() => deleteShareToFillUser(index + 1)}
                >
                  <div className="minus fa fa-minus" />
                </button>
              </p>
            ))
          }
        </div>
      );
    }

    return null;
  }

  renderSignUpButton(isPlanTotal) {
    const { isSignUp } = this.state;
    const { isLoggedIn } = this.props;
    const signUpStyle = isPlanTotal ? '' : 'no-total';

    if (!isLoggedIn) {
      return (
        <div className={`sign-up ${signUpStyle}`}>
          <SingleCheckBox
            id="checkbox-detail-form"
            onChange={() => this.setState({ isSignUp: !isSignUp })}
            checked={!!isSignUp}
          >
            Sign Up
          </SingleCheckBox>
        </div>
      );
    }

    return null;
  }

  renderUpperRightContainer() {
    const { rightStickyStyle } = this.state;

    const {
      planDetails,
      checkoutButtonDesc,
      editForm,
      category,
      values,
      shareToFillUser,
      handleUpdateInsured,
    } = this.props;

    const isInsured = shareToFillUser === 'insured';

    return (
      <Fragment>
        <div className="d-none d-lg-block">
          {!isInsured && this.renderSignUp()}
        </div>
        <div
          className={`d-none d-lg-block upper-right-container ${rightStickyStyle}`}
        >
          <div ref={node => this.upperRightContainer = node} />
          <div className="logo-container">
            <div className="logo-inner-container">
              <img className="company-logo" src={planDetails.logo} alt={`${planDetails.logoDesc}.png`} />
            </div>
            <p className="plan-name">{planDetails.planName}</p>
            {this.renderSignUpButton(planDetails.planTotal)}
            <ChangeInitialFormDataNew
              values={values}
              displayOnly={editForm.displayOnly}
              form={category}
              headerValue={editForm.headerValue}
              valuesToListen={editForm.valuesToListen}
              conditions={editForm.conditions}
            />
          </div>
          <div className="submit-container">
            <div className="sign-up-container">
              {
                planDetails.planSubTotal ?
                  <div className="cost-container">
                    <p>
                      <span className="total-text">SubTotal</span>
                      <span className="cost-text">{planDetails.planSubTotal}</span>
                    </p>
                    <p className="gst-text">GST 7% included</p>
                  </div>
                :
                  null
              }
              {
                planDetails.discountPercentage && planDetails.discountAmount ?
                  <div className="cost-container">
                    <p>
                      <span className="total-text">Discount {planDetails.discountPercentage}</span>
                      <span className="cost-text">{planDetails.discountAmount}</span>
                    </p>
                  </div>
                :
                  null
              }
            </div>
            <div className="divider" />
            <div className="sign-up-container">
              {
                planDetails.planTotal ?
                  <div className="cost-container">
                    <p>
                      <span className="total-text">Total</span>
                      <span className="final-cost-text">{planDetails.planTotal}</span>
                    </p>
                  </div>
                :
                  null
              }
              {
                !isInsured &&
                <Button
                  variant="medium-flat-green"
                  height={48}
                  onClick={this.handlePreSubmit}
                >
                  <p>{checkoutButtonDesc}</p>
                </Button>
              }
              {
                !isInsured && planDetails.cashback ?
                  <div className="cashback-earned-container">
                    <img src={LabelImg} alt="Label.svg" />
                    <span>Earn {planDetails.cashback} Cashback</span>
                  </div>
                :
                  null
              }
            </div>
          </div>
        </div>
      </Fragment>
    );
  }

  renderBottomContainer() {
    const { rightStickyStyle } = this.state;

    const {
      planDetails,
      checkoutButtonDesc,
      selectedPlanData,
      isLoggedIn,
      shareToFillUser,
    } = this.props;

    const isInsured = shareToFillUser === 'insured';

    return (
      <Fragment>
        <div
          className={`bottom-container ${rightStickyStyle}`}
        >
          <div className="bottom-inner-container">
            {
              planDetails.planTotal ?
                <div className="price-container">
                  <h5>{planDetails.planName}</h5>
                  {selectedPlanData && selectedPlanData.planType && <h6>{_.startCase(selectedPlanData.planType)}</h6>}
                  <h4>
                    <span>${planDetails.planTotal}</span>
                    {
                      planDetails.planSubTotal && <span className="price">{planDetails.planSubTotal}</span>
                    }
                  </h4>
                </div>
              :
                null
            }
            <div className={`resp-button-container ${isLoggedIn ? 'big' : 'small'}`}>
              {this.renderSignUpButton(planDetails.planTotal)}
              {!isInsured &&
                <Button
                  variant="medium-flat-green"
                  height={48}
                  onClick={this.handlePreSubmit}
                >
                  <p>{checkoutButtonDesc}</p>
                </Button>
              }
            </div>
          </div>
        </div>
      </Fragment>
    );
  }

  renderShareablePopup() {
    const { showModal } = this.state;
    const {
      editForm,
      values,
      selectedPlanData,
      getAnonymousSession,
      getSessionParams,
      shareToFillUser,
      sessionData,
      verifyHolder,
      verifyInsured,
      postCreateInsured,
      onUpdateDetailValues,
      setShareToFillParams,
    } = this.props;

    if (!selectedPlanData) {
      return null;
    }

    try {
      if (shareToFillUser === 'flow') {
        return (
          <ShareablePopUp
            showModal={showModal}
            closeModal={() => this.setState({ showModal: false })}
            valuesToListen={editForm.valuesToListen}
            values={values}
            conditions={editForm.conditions}
            productInfo={selectedPlanData.info}
            getAnonymousSession={getAnonymousSession}
            getSessionParams={getSessionParams}
          />
        );
      }

      return (
        <Insured
          productInfo={selectedPlanData && selectedPlanData.info}
          planType={selectedPlanData.planType}
          valuesToListen={editForm.valuesToListen}
          conditions={editForm.conditions}
          flowValues={values}
          sessionData={sessionData}
          setValidation={() => this.setState({ isValidated: true })}
          verifyHolder={verifyHolder}
          verifyInsured={verifyInsured}
          postCreateInsured={postCreateInsured}
          shareToFillUser={shareToFillUser}
          onUpdateDetailValues={onUpdateDetailValues}
          setShareToFillParams={setShareToFillParams}
        />
      );
    } catch (error) {
      // eslint-disable-next-line
      console.log(error);
    }


    return null;
  }

  renderShareToFillUpdate() {
    const { shareToFillUser, handleUpdateInsured } = this.props;
    const isShareToFill = shareToFillUser === 'insured' || shareToFillUser === 'holder';

    if (!isShareToFill) {
      return null;
    }

    return (
      <div className="share-update-container">
        <Button
          variant="medium-flat-green"
          width={240}
          height={48}
          onClick={handleUpdateInsured}
        >
          <p>Update</p>
        </Button>
      </div>
    );
  }

  render() {
    const { isValidated } = this.state;
    const { errorArray } = this.props;

    try {
      return (
        <Fragment>
          {
            isValidated &&
            <Row className="detail-main-container-new" noGutters>
              <div ref={node => this.upperMarker = node} />
              <Col xs={12} lg={7}>
                <div className="d-block d-lg-none">
                  {this.renderSignUp()}
                </div>
                <div className="detail-left-container">
                  {this.props.children}
                  {errorArray}
                  {this.renderShareToFillUpdate()}
                </div>
              </Col>
              <Col xs={12} lg={5}>
                {this.renderUpperRightContainer()}
                {this.renderShareToFillInfo()}
              </Col>
              <Col xs={12} className="d-block d-lg-none">
                {this.renderBottomContainer()}
              </Col>
            </Row>
          }
          {this.renderShareablePopup()}
        </Fragment>
      );
    } catch (error) {
      // eslint-disable-next-line
    }
    
    return null;
  }
}

DetailFormNew.defaultProps = {
  errorArray: null,
  checkoutButtonDesc: 'Checkout',
  editForm: null,
  selectedPlanData: null,
  values: null,
  shareToFillUser: null,
  sessionData: null,
  verifyHolder: null,
  verifyInsured: null,
  onUpdateDetailValues: null,
  deleteShareToFillUser: null,
  handleUpdateInsured: null,
};

DetailFormNew.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  planDetails: PropTypes.object.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  errorArray: PropTypes.array,
  loginControl: PropTypes.object.isRequired,
  checkoutButtonDesc: PropTypes.string,
  category: PropTypes.string.isRequired,
  editForm: PropTypes.object,
  selectedPlanData: PropTypes.object,
  values: PropTypes.object,
  getAnonymousSession: PropTypes.func.isRequired,
  getSessionParams: PropTypes.func.isRequired,
  shareToFillUser: PropTypes.string,
  sessionData: PropTypes.object,
  verifyHolder: PropTypes.func,
  verifyInsured: PropTypes.func,
  onUpdateDetailValues: PropTypes.func,
  handleUpdateInsured: PropTypes.func,
  deleteShareToFillUser: PropTypes.func,
  doSetIntercomVisibility: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  loginControl: state.login.loginControl,
});

const mapDispatchToProps = dispatch => ({
  doSetIntercomVisibility: (isVisible) => {
    dispatch(setIntercomVisibility(isVisible));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(DetailFormNew);
