/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import moment from 'moment';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import Button from 'components/FormsV3/Button/Button';

import { validateFormValue, validateForm } from 'lib/validatorsV2';
// import { getFormValue } from 'lib/helperFunctions';

import ShareableProductInfo from '../ShareableProductInfo/ShareableProductInfo';

import '../ShareablePopUp.scss';
import './ShareableHolderPopup.scss';

export const defaultForms = [{
  title: 'Date of Birth',
  size: 12,
  slug: 'birth_date',
  type: 'date',
  max_date: moment().startOf('day').toDate(),
  is_required: true,
}];

const defaultState = {
  values: {},
  errors: {},
};

class ShareableHolderPopup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      ...defaultState,
    };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.verifyHolder = this.verifyHolder.bind(this);
  }

  async onChangeValue(type, name, value) {
    const {
      values,
      errors,
    } = this.state;

    const error = errors && errors[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, values, defaultForms) };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: {
        ...errors,
        ...newError,
      },
    });
  }

  async verifyHolder() {
    const { values } = this.state;
    const { verifyHolder } = this.props;

    const newErrors = await validateForm(values, null, defaultForms);

    if (_.isEmpty(newErrors)) {
      const dateOfBirth = values.birth_date.value;
      const result = await verifyHolder(dateOfBirth);

      if (!result) {
        this.setState({
          errors: { birth_date: 'Invalid date of birth for holder.' },
        });
      }
      return;
    }

    this.setState({
      errors: newErrors,
    });
  }

  renderForm() {
    const {
      values,
      errors,
    } = this.state;

    const {
      flowValues,
      valuesToListen,
      conditions,
      productInfo,
      sessionData,
    } = this.props;

    return (
      <Row noGutters>
        <Col xs={12}>
          <h3>Create a shareable link to share with other insured!</h3>
        </Col>
        <Col xs={12}>
          <ShareableProductInfo
            valuesToListen={valuesToListen}
            flowValues={flowValues}
            conditions={conditions}
            productInfo={productInfo}
          />
        </Col>
        <Col xs={12}>
          <h3>{`Welcome Back\n${sessionData.holder_name}!`}</h3>
          <Row noGutters>
            {
              _.map(defaultForms, (form, index) => (
                <FieldGenerator
                  key={`business-form-${index}`}
                  values={values}
                  errors={errors}
                  fieldData={form}
                  onChangeValue={this.onChangeValue}
                />
              ))
            }
          </Row>
        </Col>
        <Col className="d-flex justify-content-center mt-4" xs={12}>
          <Button
            type="button"
            variant="medium-flat-green"
            height={48}
            width={168}
            onClick={this.verifyHolder}
          >
            <p>Next</p>
          </Button>
        </Col>
      </Row>
    );
  }

  render() {
    const {
      showModal,
      closeModal,
      sessionData,
    } = this.props;

    if (!sessionData) {
      return null;
    }

    return (
      <ModalPopup
        showModal={showModal}
        closeModal={closeModal}
        className="shareable-link-popup shareable-insured"
        backdropClassName="blue-modal-backdrop"
        body={this.renderForm()}
      />
    );
  }
}

ShareableHolderPopup.defaultProps = {
  showModal: false,
  conditions: null,
};

ShareableHolderPopup.propTypes = {
  showModal: PropTypes.bool,
  closeModal: PropTypes.func.isRequired,
  valuesToListen: PropTypes.array.isRequired,
  flowValues: PropTypes.object.isRequired,
  conditions: PropTypes.object,
  productInfo: PropTypes.object.isRequired,
  // history: PropTypes.object.isRequired,
  sessionData: PropTypes.object.isRequired,
  verifyHolder: PropTypes.func.isRequired,
};

export default withRouter(ShareableHolderPopup);
