/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import moment from 'moment';
import React, { Component } from 'react';
import ShareableInsuredPopup from 'components/Templates/ShareablePopUp/ShareableInsuredPopup/ShareableInsuredPopup';
import ShareableHolderPopup from 'components/Templates/ShareablePopUp/ShareableHolderPopup/ShareableHolderPopup';

import './Insured.scss';

class Insured extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showModal: true,
    };

    this.verifyInsured = this.verifyInsured.bind(this);
    this.verifyHolder = this.verifyHolder.bind(this);
    this.setValidated = this.setValidated.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { productInfo } = this.props;

    if (productInfo && !prevProps.productInfo) {
      this.setState({
        showModal: true,
      });
    }
  }

  setValidated() {
    const { setValidation } = this.props;
    this.setState({ showModal: false });
    setValidation();
  }

  async verifyInsured(name, dateOfBirth) {
    const {
      verifyInsured,
      sessionData,
      setValidation,
      postCreateInsured,
      onUpdateDetailValues,
      setShareToFillParams,
    } = this.props;

    const request = {
      session_token: sessionData.sessionID,
      name,
      birth_date: moment(dateOfBirth).format('DD/MM/YYYY'),
    };

    const result = await verifyInsured(request);

    if (result) {
      this.setValidated();
      setShareToFillParams({
        ...result,
        insured_info: [result.insured_info],
      });
      return { valid: true }
    }

    const nameExists = _.find(sessionData.insured || [], insur => (insur || '').toLowerCase() === (name || '').trim().toLowerCase());
    if (nameExists) {
      return {
        valid: false,
        error: 'Birth date authentication failed!',
      }
    }

    const postResults = await postCreateInsured(request);

    if (postResults) {
      this.setValidated();
      setShareToFillParams({
        insured_info: [{
          ...postResults,
          name,
          birth_date: dateOfBirth,
        }],
      });

      return { valid: true };
    }

    return {
      valid: false,
      error: 'Authentication failed!',
    }
  }

  async verifyHolder(dateOfBirth) {
    const {
      verifyHolder,
      sessionData,
      setValidation,
      setShareToFillParams,
    } = this.props;

    const result = await verifyHolder({
      session_token: sessionData.sessionID,
      birth_date: dateOfBirth.toISOString(),
    });
    console.log(result);
    if (result) {
      this.setValidated();
      setShareToFillParams(result);
    }

    return result;
  }

  render() {
    const { showModal } = this.state;
    const {
      flowValues,
      conditions,
      productInfo,
      valuesToListen,
      sessionData,
      isExpire,
      setValidation,
      shareToFillUser,
      verifyHolder,
      verifyInsured,
    } = this.props;

    if (!showModal) {
      return null;
    }

    if (shareToFillUser === 'insured') {
      return (
        <ShareableInsuredPopup
          showModal={showModal}
          closeModal={() => this.setState({ showModal: false })}
          valuesToListen={valuesToListen}
          flowValues={flowValues}
          conditions={conditions}
          productInfo={productInfo}
          sessionData={sessionData}
          isExpire={isExpire}
          verifyInsured={this.verifyInsured}
        />
      );
    }

    return (
      <ShareableHolderPopup
        showModal={showModal}
        closeModal={() => this.setState({ showModal: false })}
        valuesToListen={valuesToListen}
        flowValues={flowValues}
        conditions={conditions}
        productInfo={productInfo}
        sessionData={sessionData}
        isExpire={isExpire}
        verifyHolder={this.verifyHolder}
      />
    );
  }
}

Insured.defaultProps = {
};

Insured.propTypes = {
};

export default Insured;
