/**
 * TravelRouteContainer.js
 * The wrapper container for travel
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import moment from 'moment';
import ReactPixel from 'react-facebook-pixel';

// import Page from 'components/Page';
// import { travelTags } from 'metaTagsConstants';

import StepProgressBar from 'components/Templates/StepProgressBar/StepProgressBarNew';
import RefreshTimer from 'components/Templates/RefreshTimer/RefreshTimer';

import { parseTravelForm } from 'actions/travelActions';
import { getSessionParams, getInsuredSession } from 'actions/shareableLinkActions';
import { getPromoGiveaways, setInsuranceFormState, FORMSTATES, getProductDetails } from 'actions/insuranceActions';
import { routeHandler, extractSearchParams, revertFormValuesUsingDefault, revertFormValues } from 'lib/helperFunctions';
import { validateForm, validateFormValue } from 'lib/validatorsV2';

import APILoader from 'components/Templates/APILoader/APILoader';
import TravelMainContainer from './TravelMainContainer';
import TravelChoosePlanContainer from './TravelChoosePlanContainer';
import TravelDetailContainer from './TravelDetailContainer';
import TravelCheckoutContainer from './TravelCheckoutContainer';

// Default values
import {
  defaultValues,
  stepProgressNormal,
  stepProgressHolder,
  getValuesToListen,
  adultFields,
  normalIndividualFields,
} from './Travel.constants';

class TravelRouteContainer extends Component {
  // Route handler
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted, defaultRoute } = prevState;
    const {
      formState,
      match,
      history,
    } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(defaultRoute, formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }

    return null;
  }

  constructor(props) {
    super(props);
    const {
      match,
      history,
    } = this.props;

    let newValues = { ...defaultValues };

    let matchParams = extractSearchParams(history.location.search);
    if (!matchParams && match && match.params) {
      matchParams = match.params;
    }

    // Check if first load is product page
    const isProductPage = matchParams && matchParams.insurer && matchParams.product;

    // Check if is share to fill
    const shareToFillUser = matchParams && matchParams.groupbuy === 'true' && matchParams.user;

    if (isProductPage) {
      // Check if first load if from TM Japan
      const isXploraJapan = isProductPage && matchParams.insurer === 'tokio_marine' && matchParams.product === 'xplora_japan';

      // Set default values
      newValues = { ...defaultValues };
      if (isXploraJapan) {
        newValues.countries = { type: 'multiple_select', value: [{ display_name: 'Japan', value: 'Japan' }] };
        newValues.region = { type: 'select', value: { display_name: 'Asia', value: 'Asia' } };
      }
    }

    const defaultRoute = 'travel';

    this.state = {
      formIndex: 0,
      selectedArray: [null, null, null],
      selectionButtonIndex: 0,
      planType: 'essential',
      bypassParams: { ...(matchParams || {}) },
      values: newValues,
      errors: {},
      detailValues: {},
      detailErrors: {},
      plansData: null,
      selectedPlanData: null,
      charged: false,
      apiLoader: null,
      isInitialLoad: true,
      defaultRoute,
      sessionData: null,
      shareToFillUser,
    };

    if (isProductPage || shareToFillUser) {
      // Reset route
      history.replace(`/${defaultRoute}${history.location.search}`);
    }

    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderTravelForms = this.renderTravelForms.bind(this);

    this.setCharged = this.setCharged.bind(this);
    this.setSelectedArray = this.setSelectedArray.bind(this);
    this.setSelectionButtonIndex = this.setSelectionButtonIndex.bind(this);
    this.setPlanType = this.setPlanType.bind(this);
    this.setPlansData = this.setPlansData.bind(this);
    this.setSelectedPlanData = this.setSelectedPlanData.bind(this);
    this.setGroupBuy = this.setGroupBuy.bind(this);

    this.changeUserDetails = this.changeUserDetails.bind(this);
    this.handleProductPage = this.handleProductPage.bind(this);

    this.onChangeValue = this.onChangeValue.bind(this);
    this.onChangeDetailValue = this.onChangeDetailValue.bind(this);
    this.onUpdateValues = this.onUpdateValues.bind(this);
    this.onUpdateDetailValues = this.onUpdateDetailValues.bind(this);
    this.validateForm = this.validateForm.bind(this);
    this.validateDetailsForm = this.validateDetailsForm.bind(this);
    this.onUpdateSessionDataInsured = this.onUpdateSessionDataInsured.bind(this);
  }

  async componentDidMount() {
    const { bypassParams, defaultRoute, shareToFillUser } = this.state;
    const {
      history,
      setFormState,
      travelPromoGiveAway,
      doGetPromoGiveaways,
      location,
    } = this.props;

    ReactPixel.track('CustomizeProduct');

    // Prefill login details
    this.changeUserDetails();
    // Set to mounted
    this.setState({
      isMounted: true,
      apiLoader: this.APILoader,
    });

    if (!shareToFillUser) {
      // If is product page, straight away go to choose plan
      const isProductPage = bypassParams && bypassParams.insurer && bypassParams.product;
      if (isProductPage) {
        setFormState(FORMSTATES.CHOOSEPLAN.index);
        history.push(`/${defaultRoute}${FORMSTATES.CHOOSEPLAN.path}/${bypassParams.insurer}/${bypassParams.product}`);
      }

      // Get promo giveaway
      if (travelPromoGiveAway === undefined && this.APILoader) {
        doGetPromoGiveaways();
      }
    } else {
      console.log('sharetofill');
      await this.onShareToFill();
      setFormState(FORMSTATES.DETAILFORM.index);
      history.push(`/${defaultRoute}${FORMSTATES.DETAILFORM.path}${location.search}`);
    }

    window.scrollTo(0, 0);
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  async onShareToFill() {
    const { values } = this.state;
    const { location, history } = this.props;

    try {
      const searchParams = extractSearchParams(location.search);
      const sessionID = searchParams && searchParams.token;
      const data = await this.APILoader.handleAPI(() => getSessionParams(sessionID));

      if (!data) {
        history.push('/forbidden');
        return;
      }

      const sessionData = await this.APILoader.handleAPI(() => getInsuredSession(sessionID));
      sessionData.sessionID = sessionID;
      console.log(sessionData);

      const numAdult = (sessionData.insured || []).length + 1;
      const values = revertFormValuesUsingDefault(defaultValues, data.session_params, defaultValues);
      values.num_adult.value = Math.max(2, numAdult);
      console.log(values, data);

      const selectedPlanData = await this.APILoader.handleAPI(() => getProductDetails(data.client_name, data.session_params.policy_slug, parseTravelForm(values)));
      console.log(selectedPlanData);

      this.setState({
        values,
        planType: data.session_params.policy_type,
        selectedPlanData,
        // valuesToListen: getValuesToListen(flowValues),
        sessionData,
      });
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  onUpdateSessionDataInsured(userIndex) {
    const { sessionData } = this.state;

    const newInsured = [...sessionData.insured];
    newInsured.splice(userIndex, 1);

    this.setState({
      sessionData: {
        ...sessionData,
        insured: newInsured,
      },
    });
  }

  // Update form values on change
  async onChangeValue(type, name, value, fields) {
    const {
      values,
      errors,
    } = this.state;

    try {
      const error = errors && errors[name];
      let newError = {};

      if (error) {
        newError = { [name]: await validateFormValue(name, { type, value }, values, fields) };
      }

      return new Promise((resolve) => {
        this.setState({
          values: {
            ...values,
            [name]: { type, value },
          },
          errors: { ...errors, ...newError },
        }, resolve);
      });
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  async onChangeDetailValue(type, name, value, detailName, fields) {
    const {
      detailValues,
      detailErrors,
    } = this.state;

    try {
      const error = detailErrors && detailErrors[detailName] && detailErrors[detailName][name];
      let newError = {};

      if (error) {
        newError = { [name]: await validateFormValue(name, { type, value }, detailValues[detailName], fields) };
      }

      return new Promise((resolve) => {
        this.setState({
          detailValues: {
            ...detailValues,
            [detailName]: {
              ...detailValues[detailName],
              [name]: { type, value },
            },
          },
          detailErrors: {
            ...detailErrors,
            [detailName]: {
              ...detailErrors[detailName],
              ...newError,
            },
          },
        }, resolve);
      });
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  async onUpdateValues(values) {
    await new Promise((resolve) => {
      this.setState({
        values,
      }, resolve);
    });
  }

  async onUpdateDetailValues(detailValues) {
    await new Promise((resolve) => {
      this.setState({
        detailValues,
      }, resolve);
    });
  }

  setSelectedPlanData(selectedPlanData) {
    this.setState({ selectedPlanData });
  }

  setSelectedArray(array) {
    this.setState({ selectedArray: array });
  }

  setSelectionButtonIndex(index) {
    this.setState({ selectionButtonIndex: index });
  }

  setPlanType(planType) {
    this.setState({ planType });
  }

  async setCharged(charged) {
    await new Promise(resolve => this.setState({ charged }, resolve));
  }

  setPlansData(plansData) {
    if (!plansData) {
      this.setState({
        plansData: null,
      });

      return;
    }

    const newPlansData = { ...plansData };

    try {
      newPlansData.insurances_all = [...plansData.insurances, ...(plansData.insurances_prex || [])];

      _.each(newPlansData, (plansArray) => {
        _.each(plansArray, (plan) => {
          if (plan && plan.info && plan.info.insurer_slug && plan.info.product_slug) {
            // eslint-disable-next-line
            plan.info.insurer_product_slug = `${plan.info.insurer_slug}_${plan.info.product_slug}`;
          }
        });
      });
    } catch (error) {
      // eslint-disable-next-line
      // console.log(error);
    }

    this.setState({
      plansData: newPlansData,
    });
  }

  setGroupBuy() {
    this.setState({ shareToFillUser: 'flow' });
  }

  async validateForm(callback = null, fields = null, specialFields = null) {
    const {
      values,
      errors,
    } = this.state;

    let newErrors = { ...errors };
    try {
      newErrors = await validateForm(values, null, fields, specialFields);
    } catch (error) {
      // eslint-disable-next-line
    }

    return new Promise((resolve) => {
      this.setState({
        errors: newErrors,
      }, () => {
        if (_.isEmpty(newErrors)) {
          if (callback) {
            callback();
          }
          resolve(true);
        }
        resolve(false);
      });
    });
  }

  async validateDetailsForm(formName, callback = null, fields = null, specialFields = null) {
    const {
      detailValues,
      detailErrors,
    } = this.state;

    let newErrors = { ...detailErrors[formName] };
    try {
      newErrors = await validateForm(detailValues[formName], null, fields, specialFields);
    } catch (error) {
      // eslint-disable-next-line
    }

    console.log(formName, detailValues, newErrors, fields);

    return new Promise((resolve) => {
      this.setState({
        detailErrors: {
          ...detailErrors,
          [formName]: newErrors,
        },
      }, () => {
        if (_.isEmpty(newErrors)) {
          if (callback) {
            callback();
          }
          resolve(true);
        }
        resolve(false);
      });
    });
  }

  changeUserDetails() {
    const { detailValues } = this.state;
    const { userDetails } = this.props;

    if (userDetails) {
      this.setState({
        detailValues: {
          ...detailValues,
          adult_0: {
            email: { type: 'email', value: userDetails.email },
            given_name: { type: 'text', value: userDetails.firstName },
            family_name: { type: 'text', value: userDetails.surname },
            id_type: { type: 'select', value: { display_name: userDetails.id_type, value: userDetails.id_type } },
            nric: { type: 'text', value: userDetails.nric },
            nationality: { type: 'select', value: { display_name: userDetails.nationality, value: userDetails.nationality } },
            birth_date: { type: 'date', value: moment(userDetails.dateOfBirth).toDate() },
            gender: { type: 'select', value: { display_name: userDetails.gender, value: userDetails.gender } },
            marital_status: { type: 'select', value: { display_name: userDetails.marital_status, value: userDetails.marital_status } },
            race: { type: 'select', value: { display_name: userDetails.race, value: userDetails.race } },
            occupation: { type: 'select', value: { display_name: userDetails.occupation, value: userDetails.occupation } },
            mobile_number: { type: 'number', value: userDetails.contact },
            address: { type: 'text', value: userDetails.address },
            unit_no: { type: 'text', value: userDetails.unit_no },
            postal: { type: 'text', value: userDetails.postal_code },
            blk_no: { type: 'text', value: userDetails.blk_no },
          },
        },
        isInitialLoad: false,
      });
    }
  }

  handleProductPage(insurerSlug, productSlug) {
    const { defaultRoute } = this.state;
    const { history } = this.props;

    if (insurerSlug && productSlug) {
      history.push(`/${defaultRoute}${FORMSTATES.CHOOSEPLAN.path}/${insurerSlug}/${productSlug}`);
    }
  }

  renderStepProgress() {
    const { formIndex, charged, shareToFillUser } = this.state;

    if (shareToFillUser === 'insured') {
      return null;
    }

    return (
      <StepProgressBar
        formState={formIndex}
        form="travel"
        charged={charged}
        {...(shareToFillUser === 'holder' ? stepProgressHolder : stepProgressNormal)}
      />
    );
  }

  renderTravelForms() {
    const {
      formIndex,
      selectedArray,
      selectionButtonIndex,
      planType,
      values,
      errors,
      detailValues,
      detailErrors,
      mainFields,
      plansData,
      selectedPlanData,
      charged,
      apiLoader,
      defaultRoute,
      shareToFillUser,
      sessionData,
    } = this.state;

    const { userDetails } = this.props;
    const isLoggedIn = !_.isEmpty(userDetails);

    const props = {
      values,
      errors,
    };

    switch (formIndex) {
      case FORMSTATES.CHOOSEPLAN.index:
        return (
          <TravelChoosePlanContainer
            {...props}
            isLoggedIn={isLoggedIn}
            setSelectedArray={this.setSelectedArray}
            setSelectionButtonIndex={this.setSelectionButtonIndex}
            selectedArray={selectedArray}
            selectionButtonIndex={selectionButtonIndex}
            planType={planType}
            setPlanType={this.setPlanType}
            handleProductPage={this.handleProductPage}
            setSelectedPlanData={this.setSelectedPlanData}
            onChangeValue={this.onChangeValue}
            onUpdateValues={this.onUpdateValues}
            mainFields={mainFields}
            plansData={plansData}
            selectedPlanData={selectedPlanData}
            setPlansData={this.setPlansData}
            handleAPI={apiLoader ? apiLoader.handleAPI : () => {}}
            validateForm={this.validateForm}
            defaultRoute={defaultRoute}
            setGroupBuy={this.setGroupBuy}
          />
        );
      case FORMSTATES.DETAILFORM.index:
        return (
          <TravelDetailContainer
            isLoggedIn={isLoggedIn}
            planType={planType}
            piggyCallback={this.changeUserDetails}
            values={values}
            errors={errors}
            onChangeValue={this.onChangeValue}
            detailValues={detailValues}
            detailErrors={detailErrors}
            onChangeDetailValue={this.onChangeDetailValue}
            plansData={plansData}
            selectedPlanData={selectedPlanData}
            handleAPI={apiLoader ? apiLoader.handleAPI : () => {}}
            validateForm={this.validateDetailsForm}
            defaultRoute={defaultRoute}
            shareToFillUser={shareToFillUser}
            sessionData={sessionData}
            onUpdateDetailValues={this.onUpdateDetailValues}
            onUpdateValues={this.onUpdateValues}
            onUpdateSessionDataInsured={this.onUpdateSessionDataInsured}
          />
        );
      case FORMSTATES.CHECKOUT.index:
      case FORMSTATES.SUBMITTED.index:
        return (
          <TravelCheckoutContainer
            values={values}
            detailValues={detailValues}
            planType={planType}
            isLoggedIn={isLoggedIn}
            charged={charged}
            plansData={plansData}
            selectedPlanData={selectedPlanData}
            handleAPI={apiLoader ? apiLoader.handleAPI : () => {}}
            setCharged={this.setCharged}
            defaultRoute={defaultRoute}
          />
        );
      default:
        return (
          <TravelMainContainer
            isLoggedIn={isLoggedIn}
            values={values}
            errors={errors}
            onChangeValue={this.onChangeValue}
            handleAPI={apiLoader ? apiLoader.handleAPI : () => {}}
            validateForm={this.validateForm}
            setPlansData={this.setPlansData}
            defaultRoute={defaultRoute}
          />
        );
    }
  }

  render() {
    const { formIndex, apiLoader } = this.state;
    const { constants } = this.props;

    const checkoutStyle = formIndex === FORMSTATES.CHECKOUT.index ? 'travel-checkout-container' : '';

    return (
      <div
        // rest={travelTags}
        className={checkoutStyle}
      >
        {
          constants && apiLoader &&
          <Fragment>
            {this.renderStepProgress()}
            {this.renderTravelForms()}
          </Fragment>
        }
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
        <APILoader ref={(node) => { this.APILoader = node; }} />
      </div>
    );
  }
}

TravelRouteContainer.defaultProps = {
  userDetails: null,
  travelPromoGiveAway: undefined,
  constants: null,
};

TravelRouteContainer.propTypes = {
  userDetails: PropTypes.object,
  travelPromoGiveAway: PropTypes.object,
  constants: PropTypes.object,
  history: PropTypes.object.isRequired,
  setFormState: PropTypes.func.isRequired,
  match: PropTypes.object.isRequired,
  doGetPromoGiveaways: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  formState: state.insurance.formState,
  travelPromoGiveAway: state.insurance.promoGiveaways.travel,
  userDetails: state.userDetails.data,
  constants: state.insurance.constants,
  loginControl: state.login.loginControl,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  doGetPromoGiveaways: () => dispatch(getPromoGiveaways('travel')),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TravelRouteContainer));
