import Axios from '../lib/axiosFunctions';

export const getGenerateLink = async (request) => {
  try {
    const url = `${process.env.REACT_APP_API_HOST}/v2/stf/holder/invitation-link/generate`;

    const result = await Axios.POST(url, request);

    if (result && result.status === 200) {
      return result.data;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const sendEmails = async (request) => {
  try {
    const url = `${process.env.REACT_APP_API_HOST}/v2/stf/holder/invitation-link/send`;

    const result = await Axios.POST(url, request);

    if (result && result.status === 200 && result.data) {
      return result.data.sent;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

