export const putInsured = async (token, request, isConfirmed = false) => {
  try {
    const url = `${process.env.REACT_APP_API_HOST}/v2/stf/insured/${token}`;

    const result = await Axios.PUT(url, { ...request, is_confirmed: isConfirmed });
    console.log(result);

    if (result && result.status === 200 && result.data && result.data.updated) {
      return result.data;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const deleteInsured = async (token) => {
  try {
    const url = `${process.env.REACT_APP_API_HOST}/v2/stf/insured/${token}`;

    const result = await Axios.DELETE(url);
    console.log(result);

    if (result && result.status === 200 && result.data && result.data.deleted) {
      return result.data.deleted;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};
