/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import PropTypes from 'prop-types';

import ChangeInitialFormDataNew from 'components/Templates/ChangeInitialFormData/ChangeInitialFormDataNew';

import InsurersLogo from 'assets/images/InsurerLogos';

import './ShareableProductInfo.scss';

class ShareableProductInfo extends Component {
  render() {
    const {
      valuesToListen,
      flowValues,
      conditions,
      productInfo,
    } = this.props;

    try {
      const logo = InsurersLogo[productInfo.insurer_slug];
      return (
        <Row className="shareable-product-info" noGutters>
          <Col className="insurer-container" xs={12} sm={12}>
            <img src={logo} alt={`${productInfo.insurer_slug}.svg`} />
            <h4 className="d-none d-sm-block">{productInfo.product_name}</h4>
          </Col>
          <Col className="d-block d-sm-none" xs={12}>
            <h4>{productInfo.product_name}</h4>
          </Col>
          <Col xs={12} sm={12}>
            <ChangeInitialFormDataNew
              values={flowValues}
              displayOnly
              valuesToListen={valuesToListen}
              conditions={conditions}
            />
          </Col>
        </Row>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

ShareableProductInfo.defaultProps = {
};

ShareableProductInfo.propTypes = {
  valuesToListen: PropTypes.array.isRequired,
  flowValues: PropTypes.object.isRequired,
  conditions: PropTypes.object.isRequired,
  productInfo: PropTypes.object.isRequired,
};

export default ShareableProductInfo;
