/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import moment from 'moment';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import Button from 'components/FormsV3/Button/Button';


import { validateFormValue, validateForm } from 'lib/validatorsV2';
import { getFormValue } from 'lib/helperFunctions';

import ShareableProductInfo from '../ShareableProductInfo/ShareableProductInfo';

import '../ShareablePopUp.scss';
import './ShareableInsuredPopup.scss';

export const defaultForms = [{
  title: 'Date of Birth',
  size: 12,
  slug: 'birth_date',
  type: 'date',
  max_date: moment().startOf('day').toDate(),
  is_required: true,
}, {
  title: 'Enter or Select your fullname',
  size: 12,
  slug: 'name',
  type: 'select',
  is_required: true,
  is_ignore_validation: true,
}];

const defaultState = {
  values: {},
  errors: {},
  forms: null,
};

class ShareableInsuredPopup extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { sessionData } = nextProps;

    if (!prevState.forms && sessionData) {
      const forms = [...defaultForms];
      forms[1].options = _.map(sessionData.insured || {}, insur => ({ display_name: insur, value: insur }));
      return ({ forms });
    }

    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      ...defaultState,
    };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.verifyInsured = this.verifyInsured.bind(this);
  }

  async onChangeValue(type, name, value) {
    const {
      values,
      errors,
      forms,
    } = this.state;

    const error = errors && errors[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, values, forms) };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: {
        ...errors,
        ...newError,
      },
    });
  }

  async verifyInsured() {
    const { values } = this.state;
    const { verifyInsured } = this.props;

    const newErrors = await validateForm(values, ['birth_date'], defaultForms);
    const name = getFormValue(values.name);
    if (!name || name.toString().trim() === '') {
      newErrors.name = 'Field cannot be empty!';
    }

    if (_.isEmpty(newErrors)) {
      const dateOfBirth = getFormValue(values.birth_date);
      const result = await verifyInsured(name, dateOfBirth);

      if (!result.valid) {
        newErrors.birth_date = result.error;
      }
    }

    this.setState({
      errors: newErrors,
    });
  }

  renderExpired() {
    const { history } = this.props;

    return (
      <Row noGutters>
        <Col xs={12}>
          <h3>Sorry. This session has expired. Please follow up with your trip organizer.</h3>
        </Col>
        <Col className="d-flex justify-content-center mt-4" xs={12}>
          <Button
            type="button"
            variant="medium-flat-green"
            height={48}
            width={168}
            onClick={() => history.push('/')}
          >
            <p>OK</p>
          </Button>
        </Col>
      </Row>
    );
  }

  renderForm() {
    const {
      values,
      errors,
      forms,
    } = this.state;

    const {
      flowValues,
      valuesToListen,
      conditions,
      productInfo,
      sessionData,
    } = this.props;

    return (
      <Row noGutters>
        <Col xs={12}>
          <h3>{sessionData.holder_name} invites you to get covered for your Trip!</h3>
          <h3>Simply fill in your personal details and {sessionData.holder_name} will do the rest!</h3>
        </Col>
        <Col xs={12}>
          <ShareableProductInfo
            valuesToListen={valuesToListen}
            flowValues={flowValues}
            conditions={conditions}
            productInfo={productInfo}
          />
        </Col>
        <Col xs={12}>
          <Row noGutters>
            {
              _.map(forms, (form, index) => (
                <FieldGenerator
                  key={`business-form-${index}`}
                  values={values}
                  errors={errors}
                  fieldData={form}
                  onChangeValue={this.onChangeValue}
                />
              ))
            }
          </Row>
        </Col>
        <Col className="d-flex justify-content-center mt-4" xs={12}>
          <Button
            type="button"
            variant="medium-flat-green"
            height={48}
            width={168}
            onClick={this.verifyInsured}
          >
            <p>Next</p>
          </Button>
        </Col>
      </Row>
    );
  }

  render() {
    const {
      showModal,
      closeModal,
      sessionData,
    } = this.props;

    return (
      <ModalPopup
        showModal={showModal}
        closeModal={closeModal}
        className="shareable-link-popup shareable-insured"
        backdropClassName="blue-modal-backdrop"
        body={sessionData.valid ? this.renderForm() : this.renderExpired()}
      />
    );
  }
}

ShareableInsuredPopup.defaultProps = {
  showModal: false,
  conditions: null,
};

ShareableInsuredPopup.propTypes = {
  showModal: PropTypes.bool,
  closeModal: PropTypes.func.isRequired,
  valuesToListen: PropTypes.array.isRequired,
  flowValues: PropTypes.object.isRequired,
  conditions: PropTypes.object,
  productInfo: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  sessionData: PropTypes.object.isRequired,
  verifyInsured: PropTypes.func.isRequired,
};

export default withRouter(ShareableInsuredPopup);
